@font-face {
  font-family: "Bebas Neue";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/bebas-neue-v2-latin-regular.eot");
  /* IE9 Compat Modes */
  src: local("Bebas"), url("../fonts/bebas-neue-v2-latin-regular.woff2") format("woff2"), url("../fonts/bebas-neue-v2-latin-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.eot");
  src: local("Roboto"), url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin_cyrillic-regular.woff") format("woff"); }

body {
  margin: 0; }

.main-block {
  background-image: url("../img/main-block-background_desktop.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: #b80027;
  background-position: center;
  min-height: 658px; }

.main-block__wrapper {
  max-width: 1218px;
  margin: 0 auto;
  position: relative; }

.main-block__header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-top: 30px;
  margin-right: 30px;
  margin-left: 30px; }

.main-block__phone {
  margin-top: -5px; }

.main-block__phone a {
  text-decoration: none;
  color: #ffffff;
  font-size: 26px;
  font-family: "Bebas Neue", "Arial", cursive; }

.main-block__text {
  max-width: 397px;
  margin-top: 59px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin-right: 30px;
  margin-left: 30px;
  font-family: "Bebas Neue", "Arial", cursive; }

.main-block__city {
  font-size: 47px;
  line-height: 59px;
  text-transform: uppercase;
  margin-bottom: 5px; }

.main-block__name {
  font-size: 109px;
  font-weight: 700;
  line-height: 109px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 56px;
  letter-spacing: -12px; }

.main-block__city,
.main-block__name {
  text-align: right; }

.main-block__services-list {
  position: absolute;
  top: 242px;
  left: 23px; }

.main-block__services-list p {
  font-size: 12px;
  text-align: right;
  margin-bottom: 7px;
  margin-top: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif; }

.main-block__text,
.main-block__services-list {
  color: #ffffff; }

.main-block__button {
  text-decoration: none;
  font-size: 11px;
  color: #ffffff;
  text-transform: uppercase;
  border: 4px solid #ffffff;
  padding: 20px 54px;
  max-width: 230px;
  -ms-flex-item-align: end;
      align-self: flex-end;
  font-family: "Roboto Condensed", "Arial", sans-serif; }

.main-block__picture {
  position: absolute;
  top: 51px;
  right: 224px; }

.main-block__social {
  position: absolute;
  right: 30px;
  bottom: -18px; }

.main-block__social-list {
  list-style: none;
  margin: 0; }

.main-block__social-list li {
  margin-bottom: 15px; }

.main-block__social-link {
  text-decoration: none;
  color: #ffffff;
  font-size: 11px;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif; }

@media (max-width: 1199px) {
  .main-block {
    min-height: 491px; }
  .main-block__wrapper {
    max-width: 1199px;
    margin: 0 auto; }
  .main-block__phone a {
    font-size: 24px; }
  .main-block__social {
    display: none; }
  .main-block__picture {
    top: 72px;
    right: 30px; }
  .main-block__city {
    font-size: 34px;
    line-height: 40px;
    margin-bottom: 0; }
  .main-block__name {
    font-size: 86px;
    font-weight: 700;
    line-height: 86px;
    margin-top: 0;
    margin-bottom: 43px;
    letter-spacing: -12px; }
  .main-block__text {
    max-width: 324px;
    margin-top: 34px; }
  .main-block__services-list {
    top: 172px;
    left: 3px; }
  .main-block__button {
    text-decoration: none;
    font-size: 11px;
    color: #ffffff;
    text-transform: uppercase;
    border: 4px solid #ffffff;
    padding: 20px 32px; }
  .main-block__img {
    width: 324px;
    height: 390px; } }

@media (max-width: 767px) {
  .main-block__picture {
    display: none; }
  .main-block__wrapper {
    max-width: 767px;
    margin: 0 auto; }
  .main-block__header {
    padding-top: 19px; }
  .main-block__phone {
    margin-top: -3px; }
  .main-block__phone a {
    font-size: 20px; }
  .main-block__city {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 7px; }
  .main-block__name {
    font-size: 78px;
    line-height: 78px; }
  .main-block__text,
  .main-block__header {
    margin-right: 15px;
    margin-left: 15px; }
  .main-block__text {
    margin-top: 40px; }
  .main-block__button {
    font-size: 11px;
    padding: 20px 83px; }
  .main-block__services-list {
    top: 153px;
    left: 0; } }
